#skills-head {
  padding: 40px;
  margin: 1.2vw 0 1.2vw 0;
  /* height: 250px; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("../../drawables/skills/background.svg") no-repeat center
    center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  text-align: center;
}

#skills-title,
#skills-sub-title {
  font-style: normal;
  color: #ffffff;
}

#skills-title {
  font-family: Monoton;
  font-size: 110px;
  line-height: 210px;
}

#skills-sub-title {
  font-family: Roboto;
  font-weight: 300;
  font-size: 38px;
  line-height: 45px;
}

#skills-body {
  padding: 1.2vw;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas:
    "frontend backend"
    "devops backend";
}

#frontend-skills {
  grid-area: frontend;
  /* grid-column: span 1 / span 1;
  grid-row: span 1 / span 1; */
}

#backend-skills {
  grid-area: backend;
  /* grid-column: span 1 / span 1;
  grid-row: span 1 / span 2; */
}

#devops-skills {
  grid-area: devops;
  /* grid-row: span 2 / span 2;
  grid-column: span 1 / span 1; */
}

@media screen and (max-width: 1150px) {
  #skills-title {
    font-size: 110px;
  }
}

@media screen and (max-width: 950px) {
  #skills-body {
    padding: 1.2vw;
    /* padding: 4vw; */
  }

  #skills-head {
    padding: 10px;
    height: 190px;
  }

  #skills-title {
    font-size: 82px;
    line-height: 120px;
  }

  #skills-sub-title {
    font-weight: 300;
    font-size: 22px;
    line-height: 33px;
  }
}

@media screen and (max-width: 750px) {
  #skills-body {
    padding: 1.2vw;
    /* padding: 4vw; */
  }

  #skills-head {
    padding: 10px;
    height: 142px;
  }

  #skills-title {
    font-size: 52px;
    line-height: 90px;
  }

  #skills-sub-title {
    font-weight: 300;
    font-size: 16px;
    line-height: 25px;
  }

  #skills-body {
    /* padding: 1.2vw; */
    /* flex-wrap: wrap; */
    /* flex-direction: row; */
    /* justify-content: space-between; */
    grid-template-rows: repeat(2, auto);
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas:
      "frontend devops"
      "backend backend";
  }
}

@media screen and (max-width: 550px) {
  #skills-body {
    /* padding: 1.2vw; */
    /* flex-wrap: wrap; */
    /* flex-direction: row; */
    /* justify-content: space-between; */
    grid-template-rows: repeat(3, auto);
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas:
      "frontend"
      "devops"
      "backend";
  }
}

@media screen and (max-width: 400px) {
  #skills-body {
    /* padding: 4vw; */
  }

  #skills-head {
    padding: 10px;
    height: 106px;
  }

  #skills-title {
    font-size: 44px;
    line-height: 68px;
  }

  #skills-sub-title {
    font-weight: 300;
    font-size: 14px;
    line-height: 19px;
  }

  #skills-body {
    /* padding: 1.2vw; */
    /* flex-wrap: wrap; */
    /* flex-direction: row; */
    /* justify-content: space-between; */
    grid-template-rows: repeat(3, auto);
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas:
      "frontend"
      "devops"
      "backend";
  }
}
