#experience-head {
  padding: 40px;
  margin: 1.2vw 0 1.2vw 0;
  /* height: 250px; */
  justify-content: center;
  align-items: center;
  background: url("../../drawables/experience/background.svg") no-repeat center
    center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

#experience-title {
  font-style: normal;
  font-family: Monoton;
  font-size: 110px;
  line-height: 210px;
  color: #ffffff;
}

#experience-body {
  padding: 0 2.4vw 2.4vw 2.4vw;
}

/*INTERNSHIP SECTION*/
#internship-body {
  flex-direction: row;
}

#internships {
  flex-direction: column;
  justify-content: space-between;
  margin: 20px 0;
  width: 100%;
}

/*FULLTIME SECTION*/
#full-time-body {
  flex-direction: column;
  justify-content: center;
}

@media screen and (max-width: 1150px) {
  #experience-title {
    font-size: 110px;
  }

  #side-image {
    width: 40vw;
  }
}

@media screen and (max-width: 950px) {
  #experience-head {
    padding: 10px;
    height: 190px;
  }

  #experience-title {
    font-size: 82px;
  }

  #side-image {
    width: 38vw;
  }
}

@media screen and (max-width: 750px) {
  #experience-body {
    padding: 4vw;
  }

  #experience-head {
    padding: 10px;
    height: 142px;
  }

  #experience-title {
    font-size: 52px;
    line-height: 90px;
  }

  #internships {
    margin: 10px 0;
  }

  #side-image {
    width: 34vw;
  }
}

@media screen and (max-width: 600px) {
  #side-image {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  #experience-body {
    padding: 4vw;
  }

  #experience-head {
    padding: 10px;
    height: 106px;
  }

  #experience-title {
    font-size: 44px;
    line-height: 68px;
  }

  #internships {
    margin: 10px 0;
  }
}
