#footer {
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
  margin: 1.2vw 0vw 1.2vw 0vw;
  padding: 40px;
  /* padding: 2.4vw; */
  height: 200px;
  background-color: #000;
}

#row1 {
  font-family: Courgette;
  color: #fff;
  font-size: 24px;
  padding: 1.2vw 0 0.6vw 0;
  letter-spacing: 0.18em;
}

#row2 {
  flex-direction: row;
  justify-content: space-between;
  padding: 0.6vw 0 1.2vw 0;
}

/* hr {
  height: 1px;
  background-color: #a3a3a3;
  border: 0;
} */

@media screen and (max-width: 1150px) {
  #footer {
    padding: 30px;
  }
}

@media screen and (max-width: 950px) {
  #footer {
    padding: 30px 40px;
    height: 160px;
  }

  #row1 {
    font-size: 20px;
    padding: 0 0 0.6vw 0;
  }

  #row2 {
    padding: 0.6vw 0 0 0;
  }
}

@media screen and (max-width: 750px) {
  #footer {
    padding: 30px;
    height: 140px;
  }

  #row1 {
    font-size: 20px;
  }
}

@media screen and (max-width: 550px) {
  #footer {
    padding: 26px;
    height: 120px;
  }

  #row1 {
    font-size: 16px;
    letter-spacing: 0.12em;
  }
}

@media screen and (max-width: 480px) {
  #row1 {
    margin-left: auto;
    margin-right: auto;
  }
  #row2 {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

@media screen and (max-width: 450px) {
  #footer {
    padding: 24px;
    height: 130px;
  }

  #row1 {
    font-size: 16px;
    letter-spacing: 0.12em;
  }
}

@media screen and (max-width: 350px) {
  #footer {
    padding: 24px;
    height: 130px;
  }

  #row1 {
    font-size: 14px;
    letter-spacing: 0.12em;
  }
}
