@import url(https://fonts.googleapis.com/css2?family=Bungee&family=Comfortaa:wght@300;700&family=Courgette&family=Kaushan+Script&family=Monoton&family=Patrick+Hand&family=Righteous&family=Roboto:wght@100;300;700&family=Special+Elite&family=Ubuntu:wght@300;700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Ubuntu";
}

body {
  background-color: #ffffff;
  height: auto;
  font-size: 16px;
  overflow-x: hidden; /* Hide horizontal scrollbar */
}

#top-nav {
  position: fixed;
  height: 100px;
  top: 0;
  width: 97.6vw;
  padding: 0 5vw 1.2vw 5vw;
  overflow: hidden;
  align-items: end;
  justify-content: space-between;
  background-color: #ffffff;
  transition: top 0.5s; /* Transition effect when sliding down (and up) */
}

#logo,
.top-nav-link {
  line-height: auto;
  cursor: pointer;
}

#logo {
  font-size: 2em;
  font-family: Righteous;
  letter-spacing: 0.08em;
  color: #ff0303;
}

.top-nav-link {
  font-family: Courgette;
  letter-spacing: 0%;
  font-size: 1.5em;
  color: #000000;
  margin-left: 3vw;
}

.top-nav-link:hover {
  opacity: 0.7;
}

#hamburger {
  display: none;
}

@media screen and (max-width: 1150px) {
  #logo,
  .top-nav-link {
    font-size: 24px;
    line-height: 23px;
  }
}

@media screen and (max-width: 950px) {
  #logo,
  .top-nav-link {
    font-size: 18px;
    line-height: 17px;
  }
}

@media screen and (max-width: 750px) {
  #hamburger {
    display: block;
    background-color: #ffffff;
  }

  #horizontal-menu,
  #logo {
    display: none;
  }

  #top-nav {
    height: 60px !important;
    padding: 0 5vw 0 5vw;
    align-items: center;
  }

  .top-nav-link {
    display: flex;
    align-items: center;
    margin: 0;
    font-size: 18px;
    line-height: 17px;
  }

  .top-nav-link:hover {
    opacity: 1;
    color: #898383;
    transition: linear 0.2s;
  }
}

#about-section {
  flex-direction: row;
  justify-content: center;
  align-items: center;
