  letter-spacing: 0.18em;
  font-weight: 300;
  font-size: 1.586em; /*25.375px*/
  line-height: 59px;
}

#role span a {
  text-transform: uppercase;
  text-decoration: none;
  color: #ff0000;
}

#role span a:hover {
  text-decoration: underline;
}

