#skills-head {
  padding: 40px;
  margin: 1.2vw 0 1.2vw 0;
  /* height: 250px; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url(/portfolio/static/media/background.c67d9c1d.svg) no-repeat center
    center fixed;
  background-size: cover;
  text-align: center;
}

#skills-title,
#skills-sub-title {
  font-style: normal;
  color: #ffffff;
}

#skills-title {
  font-family: Monoton;
  font-size: 110px;
  line-height: 210px;
}

#skills-sub-title {
  font-family: Roboto;
  font-weight: 300;
  font-size: 38px;
  line-height: 45px;
}

#skills-body {
  padding: 1.2vw;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas:
    "frontend backend"
    "devops backend";
}

#frontend-skills {
  grid-area: frontend;
  /* grid-column: span 1 / span 1;
  grid-row: span 1 / span 1; */
}

#backend-skills {
  grid-area: backend;
  /* grid-column: span 1 / span 1;
  grid-row: span 1 / span 2; */
}

#devops-skills {
  grid-area: devops;
  /* grid-row: span 2 / span 2;
  grid-column: span 1 / span 1; */
}

