.internship {
  padding: 20px;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  /* background-color: #f2e2e2; */
}

.internship-details {
  padding-left: 20px;
  flex-direction: column;
  justify-content: space-between;
}

.intership-company-name {
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 37px;
  /* color: #217c9d; */
}

.role-at-internship,
.internship-duration {
  font-style: normal;
  font-weight: 200;
  /* opacity: 0.8; */
  font-size: 32px;
  line-height: 37px;
  /* color: #000000; */
}

@media screen and (max-width: 1150px) {
  .intership-company-logo {
    width: 20%;
  }
  .intership-company-name,
  .role-at-internship,
  .internship-duration {
    font-size: 24px;
    line-height: 32px;
  }
}

@media screen and (max-width: 950px) {
  .internship {
    padding: 10px;
  }

  .intership-company-logo {
    width: 20%;
  }
  .intership-company-name,
  .role-at-internship,
  .internship-duration {
    font-size: 18px;
    line-height: 24px;
  }
}

@media screen and (max-width: 750px) {
  .intership-company-logo {
    width: 15%;
  }
  .intership-company-name,
  .role-at-internship,
  .internship-duration {
    font-size: 14px;
    line-height: 18px;
  }
}

@media screen and (max-width: 620px) {
  .internship {
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 480px) {
}
