#achievements-head {
  padding: 40px;
  margin: 1.2vw 0 1.2vw 0;
  /* height: 250px; */
  justify-content: center;
  align-items: center;
  background-color: #bcdcfb;
  border-top: 0.8vw solid #0e74d8;
  border-bottom: 0.8vw solid #0e74d8;
}

#achievements-title {
  font-family: Monoton;
  font-style: normal;
  font-size: 110px;
  text-align: center;
  line-height: 180px;
  color: #ffffff;
}

#achievements-body {
  /* padding: 0 2.4vw 2.4vw 2.4vw; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0e74d8;
}

@media screen and (max-width: 1150px) {
  #achievements-title {
    font-size: 110px;
  }
}

@media screen and (max-width: 950px) {
  #achievements-head {
    padding: 10px;
    height: 190px;
  }

  #achievements-title {
    font-size: 82px;
  }
}

@media screen and (max-width: 750px) {
  #achievements-head {
    padding: 10px;
    height: 142px;
    border-top: 1vw solid #0e74d8;
    border-bottom: 1vw solid #0e74d8;
  }

  #achievements-title {
    font-size: 52px;
    line-height: 90px;
  }
}

@media screen and (max-width: 500px) {
  #achievements-head {
    border-top: 1.2vw solid #0e74d8;
    border-bottom: 1.2vw solid #0e74d8;
  }
}

@media screen and (max-width: 450px) {
  #achievements-head {
    padding: 10px;
    height: 106px;
    border-top: 1.6vw solid #0e74d8;
    border-bottom: 1.6vw solid #0e74d8;
  }

  #achievements-title {
    font-size: 44px;
    line-height: 68px;
  }
}

@media screen and (max-width: 350px) {
  #achievements-title {
    font-size: 36px;
    line-height: 68px;
  }
}
